@import url(https://fonts.googleapis.com/css?family=Varela+Round);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --base-dark: #000;
  --base-light: #fff;
  --pink: #FC1B53;
  --gray-1: #191919;
  --gray-2: #333333;

  --header-height: 50px;
  --profile-width: 350px;
}

@media (max-width: 992px) {
  :root {
    --profile-width: 260px;
  }
}

.header {
  width: 100%;
  height: var(--header-height);
  background-color: var(--pink);
  position: fixed;
  top: 0;
  z-index: 10;
  box-shadow: 1px 1px 10px rgba(82, 109, 194, 0.1);

  display: -webkit-flex;

  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

.header > .logo {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  margin-left: 10px;
}

.header > .logo img {
  width: 22px;
  cursor: pointer;
}

.header > .logout {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  -webkit-align-items: center;
          align-items: center;
  margin-right: 20px;
}

.header > .logout button {
  border: 0;
  background: transparent;
  color: var(--base-light);
  cursor: pointer;
}

.login {
  width: 100%;
  height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding-top: var(--header-height);
  background-color: var(--base-dark);
  color: var(--base-light);
}

.login-box {
  width: 300px;
  height: 160px;
  padding: 15px;
  border-radius: 6px;

  display: -webkit-flex;

  display: flex;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  -webkit-align-items: center;
          align-items: center;
  -webkit-flex-direction: column;
          flex-direction: column;
  position: relative;
  background-color: var(--gray-1);
}

.github-logo > svg {
  border: 5px solid white;
  border-radius: 100%;
  background: #fff;
  width: 100px;
  height: 100px;
  margin-bottom: 5px;
}

.github-input {
  width: 100%;
  border-radius: 40px;
  border: 0;
  height: 40px;
  padding: 15px;
  margin-top: 5px;
  margin-bottom: 5px;
  background-color: var(--base-light);
  color: var(--base-dark);
}

.github-button {
  margin-top: 5px;
  width: 100%;
  padding: 10px;
  border: 0;
  cursor: pointer;
  color: var(--base-light);
  background-color: var(--pink);
  border-radius: 2px;
}

.github-button:disabled {
  cursor: not-allowed;
}

.feed {
  display: -webkit-flex;
  display: flex;
  padding-top: var(--header-height);
}

/* @media (max-width: 992px) {
  .feed > .posts {
    flex-basis: calc(100% - var(--profile-width));
    width: calc(100% - var(--profile-width));
  }
} */

@media (max-width: 768px) {
  .feed > .posts {
    width: 100%;
    -webkit-flex-basis: 100%;
            flex-basis: 100%;
  }
}


/* @media (max-width: 992px) {
  .feed > .profile {
    width: var(--profile-width);
    flex-basis: var(--profile-width);
  }
} */

@media (max-width: 768px) {
  .feed > .profile {
    display: none;
  }
}

.posts {
  background-color: var(--base-dark);
  -webkit-flex-basis: calc(100% - var(--profile-width));
          flex-basis: calc(100% - var(--profile-width));
  width: calc(100% - var(--profile-width));
}

.posts > .container {
  width: 80%;
  margin: 30px auto;
  max-width: 1000px;
}

.post {
  width: 100%;
  background-color: var(--gray-1);
  padding: 20px;
  border-radius: 6px;
  box-shadow: 1px 1px 10px rgba(82, 109, 194, 0.1);
  margin-top: 20px;
  margin-bottom: 20px;
}

.post:last-child {
  margin-bottom: 0;
}

.post > .post-header {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
}

.post > .post-header .post-header-image-board {
  width: 48px;
  height: 48px;
  border-radius: 100%;
  overflow: hidden;

  display: -webkit-flex;

  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;

  box-shadow: 1px 1px 10px rgba(82, 109, 194, 0.1);
}

.post > .post-header .post-header-image-board img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 100%;
}

.post > .post-header .post-header-profile-name {
  margin-left: 10px;
  color: var(--base-light);
}

.post > .post-header .post-header-profile-name .profile-name {
  cursor: pointer;
  margin-bottom: 2px;
  font-weight: 600;
}

.post > .post-header .post-header-profile-name .post-date {
  font-size: 0.75rem;
}

.post > .post-content {
  color: var(--base-light);
  margin: 15px 0;
}

.post > .post-image .post-image-board {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  max-height: 420px;
  overflow: hidden;
  border-radius: 6px;
}

.post > .post-image img {
  width: 100%;
  min-width: 400px;
}

.post > .post-action {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding: 20px 0;
}

.post > .post-action .post-like-icon,
.post > .post-action .post-comment-icon
{
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  margin-right: 10px;
  cursor: pointer;
}
.post > .post-action .post-like-icon svg,
.post > .post-action .post-comment-icon svg {
  max-width: 16px;
  width: 100%;
  margin-right: 5px;
  fill: #7f8f94;
  transition: all 0.4s ease-in-out;
}

.post > .post-action .post-like-icon span,
.post > .post-action .post-comment-icon span {
  font-size: 0.77rem;
  height: 10px;
  color: #7f8f94;
  transition: color 0.4s ease-in-out;
}


.post > .post-action .post-like-icon svg,
.post > .post-action .post-comment-icon svg {
  fill: var(--base-light);
}

.post > .post-action .post-like-icon.liked svg,
.post > .post-action .post-comment-icon.commented svg {
  fill: var(--pink);
}

.post > .post-action .post-like-icon.liked:hover svg,
.post > .post-action .post-comment-icon.commented:hover svg {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.post > .post-action .post-like-icon.liked span,
.post > .post-action .post-comment-icon.commented span {
  color: var(--base-light);
}

.post > .post-comments .comment-image-board {
  width: 35px;
  height: 35px;
  border-radius: 100%;
  overflow: hidden;

  display: -webkit-flex;

  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  box-shadow: 1px 1px 10px rgba(82, 109, 194, 0.1);
}

.post > .post-comments .comment-image-board img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 100%;
}

.post > .post-comments .comment {
  display: -webkit-flex;
  display: flex;
  padding: 10px;
  background-color: var(--gray-2);
  color: var(--base-light);
  margin-bottom: 10px;
  border-radius: 6px;
}

.post > .post-comments .comment .comment-profile-image {
  margin-right: 10px;
}

.post > .post-comments .comment .comment-profile-name {
  cursor: pointer;
  margin-bottom: 2px;
  font-weight: 600;
  font-size: 0.88rem;
}

.post > .post-comments .comment .comment-text {
  font-size: 0.88rem;
}

.post > .post-send-comment .post-send-form {
  position: relative;
  width: 100%;
}

.post > .post-send-comment .post-send-form input {
  width: 100%;
  border-radius: 40px;
  border: 0;
  height: 40px;
  padding: 15px;
  margin-top: 10px;
  background-color: var(--base-light);
  color: var(--base-dark);
}

.post > .post-send-comment .post-send-form button {
  position: absolute;
  top: 0;
  right: 0;
  display: none;
}

.profile {
  width: var(--profile-width);
  -webkit-flex-basis: var(--profile-width);
          flex-basis: var(--profile-width);
  min-height: 100vh;
  padding: 50px;
  z-index: 5;
  box-shadow: 1px 1px 10px rgba(82, 109, 194, 0.1);
  background-color: var(--gray-1);
  position: fixed;
  right: 0;
}

.profile > .profile-image {
  width: 120px;
  height: 120px;
  margin: 0 auto;
  position: relative;
}

.profile > .profile-image .image-board {
  width: 120px;
  height: 120px;
}

.profile > .profile-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 100%;
}


.profile > .profile-name {
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  margin-top: 20px;
  font-weight: 600;
  color: var(--base-light);
}

.profile > .profile-stats {
  display: -webkit-flex;
  display: flex;
  margin: 20px 0;
}

.profile > .profile-stats > div {
  -webkit-flex: 4 1;
          flex: 4 1;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
}

.profile > .profile-stats > div > span:nth-child(1) {
  color: var(--pink);
}

.profile > .profile-stats > div > span:nth-child(2) {
  font-size: 0.66rem;
  text-transform: uppercase;
  padding: 5px;
  color: var(--base-light);
}

.typing {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  position: absolute;
  top: 0;
  right: 0;
  z-index: 15;
  border-radius: 20px;
  padding: 8px;
  background: var(--pink);
  -webkit-transform: scale(0);
          transform: scale(0);
  transition: all 0.3s ease-in-out;
}

.typing.active {
  -webkit-transform: scale(1);
          transform: scale(1);
}

.typing > .dot {
  width: 6px;
  height: 6px;
  border-radius: 100%;
  margin-right: 3px;
  background: var(--base-light);
  -webkit-animation: wave 1s linear infinite;
          animation: wave 1s linear infinite;
}

.typing .dot:nth-child(2) {
  -webkit-animation-delay: -0.9s;
          animation-delay: -0.9s;
}

.typing .dot:nth-child(3) {
  -webkit-animation-delay: -0.7s;
          animation-delay: -0.7s;
  margin-right: 0px;
}

@-webkit-keyframes wave {
  0%,
  60%,
  100% {
    -webkit-transform: none;
            transform: none;
    -webkit-transform: initial;
            transform: initial;
  }

  30% {
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
  }
}

@keyframes wave {
  0%,
  60%,
  100% {
    -webkit-transform: none;
            transform: none;
    -webkit-transform: initial;
            transform: initial;
  }

  30% {
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
  }
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html,
body {
  font-size: 16px;
  font-family: 'Varela Round';
  color: var(--base-light);
  background-color: var(--base-dark);
}

@media (max-width: 600px) {
  html,
  body {
    font-size: 18px;
  }
}

